// Typography  
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6
  word-break: break-word
  margin-bottom: 0.5rem
  font-weight: $font-bold
  line-height: 1.4


h1, .h1
  font-size: 3.125rem // 50px

h2, .h2
  font-size: 2.5rem // 40px

h3, .h3
  font-size: 2rem // 32px

h4, .h4
  font-size: 1.5rem // 24px

h5, .h5
  font-size: 1.125rem // 18px

h5.sub-title, .h5.sub-title
  font-weight: normal

h6, .h6
  font-size: 1rem // 16px
  font-weight: normal
  line-height: 1.68

p, .body-1
  font-size: 1rem // 16px
  line-height: 1.68


small, .small, .caption1
  font-size: 0.875rem // 14px
  font-weight: 400

.font-12, .caption2
  font-size: 0.75rem // 12px

.serif
  font-family: Montserrat, serif



@media (max-width: 960px)
  h1, .h1
    font-size: 2.5rem // 40px
  h2, .h2
    font-size: 2.25rem // 36px
  h3, .h3
    font-size: 1.875rem // 30px
  h4, .h4
    font-size: 1.375rem // 22px
  h5, .h5
    font-size: 1rem // 16px
  p, .body-1
    font-size: 0.875rem // 14px


@media (max-width: 375px)
  h1, .h1
    font-size: 2.25rem // 36px
  h2, .h2
    font-size: 2rem // 32px
  h3, .h3
    font-size: 1.75rem // 28px
  h4, .h4
    font-size: 1.25rem // 20px
  h5, .h5
    font-size: 1rem // 16px
  p, .body-1
    font-size: 0.875rem // 14px


