@charset "UTF-8"

@import "./function/variables"

@import "./globals/reboot"
@import "./globals/utilities"
@import "./globals/grid"
@import "./globals/typography"


html, body
  font-family: "Museo Sans", "Noto Sans CJK TC", sans-serif

img
  max-width: 100%

.text-orange
  color: $primary !important
.text-white
  color: $white !important
.text-gray
  color: $secondary !important

.bg-orange
  background: $primary !important
.bg-gray 
  background: $secondary !important


.one-point-orange-before
  padding-left: 8px
  position: relative
  &:before
    position: relative
    left: -8px
    content: url("../img/icon/one-point-orange-md.svg")

.two-point-orange-before
  padding-left: 8px
  position: relative
  &:before
    position: relative
    top: 2px
    left: -6px
    content: url("../img/icon/two-point-orange-md.svg")

.two-point-orange-after
  padding-right: 8px
  position: relative
  &:after
    position: relative
    left: 8px
    content: url("../img/icon/two-point-orange-md.svg")

.two-point-gray-before
  padding-: 8px
  position: relative
  &:before
    position: relative
    left: -8px
    content: url("../img/icon/two-point-gray-md.svg")

.two-point-gray-after
  padding-right: 8px
  position: relative
  &:after
    position: relative
    left: 8px
    content: url("../img/icon/two-point-gray-md.svg")


.arrow-circle-link
  img
    vertical-align: text-bottom



.wrap
  overflow: hidden


.grid-line
  .horizontal-wrap 
    width: 100%
    height: 100%
    display: flex
    justify-content: space-between
    position: fixed
    top: 0
    left: 0
    flex-flow: column
    z-index: 2
    .line
      height: 1px
      width: 100%
      opacity: .5
      background: #E4E9EF
      &:nth-child(n+7)
        display: none
        

  .vertical-wrap
    width: 100%
    height: 100%
    display: flex
    justify-content: space-between
    position: fixed
    top: 0
    left: 0
    z-index: 1
    .container
      display: flex
      justify-content: space-between
    .line
      width: 1px
      height: 100%
      opacity: .5
      background: #E4E9EF



.wrap
  position: relative
  z-index: 2


.m-menu
  display: none




.side-menu
  width: 136px
  height: 100%
  position: fixed
  z-index: 5
  top: 0
  left: 0
  background: #E3E9EE
  .logo
    width: 136px
    height: 136px
    display: flex
    align-items: center
    justify-content: center
    background: #242628
    img
      width: 72px
      height: 72px

  .nav
    height: calc(100% - 136px - 88px)
    display: flex
    align-items: center
    justify-content: center 
    ul
      padding-left: 0
      margin-bottom: 0
    li
      padding: 8px 0
      list-style: none
      position: relative
      a
        display: inline-block
        padding: 0
        font-size: 14px
        color: #242628
        &:hover
          text-decoration: none

    li.active
      a
        color: $orange
      &:before
        position: absolute
        top: 6px
        left: -10px
        content: url("../img/icon/one-point-orange.svg")
          

  .talk-to-us
    width: 136px
    padding: 18px 0
    color: #fff
    text-align: center
    background: #FF5036
    position: fixed
    bottom: 0
    img
      margin-right: 8px
    span
      font-size: 14px




.committed
  padding-top: 120px
  padding-bottom: 120px
  background: url("../img/md/committed-bg.png") right -33px no-repeat
  background-size: 36%
  .bg-orange
    width: calc(100% - 30px)
    height: 100%
    position: absolute
    top: 20px
    left: -5px
    z-index: 1

  .committedSwiper
    position: relative
    z-index: 2
    border: 1px solid $orange

  .scroll-down
    margin-top: 40px


.value
  padding-top: 120px
  padding-bottom: 120px
  .bg-orange
    width: 80%
    height: 400px
    position: relative
    top: 0px
    left: 0
    z-index: 2
  .bg-gray
    width: 80%
    height: 400px
    position: absolute
    top: 80px
    left: 48px
    z-index: 1
  .text-wrap
    margin-left: 48px
    position: absolute
    top: 32px
    z-index: 3
    h5.sub-title
      margin-right: 30%
      margin-bottom: 48px
      text-align: right
    .title
      .two-point-gray-after
        margin-bottom: 16px

  .list
    display: flex
    margin-bottom: 64px
    .list-img
      margin-right: 24px
      img
        width: 95px
        max-width: 96px
    &:last-child
      margin-bottom: 0


.service
  padding-top: 60px
  padding-bottom: 120px
  .bg-gray
    padding: 32px
    height: 200px
    position: relative
  .bg-orange
    padding: 32px  
    width: 700px
    height: 140px
    position: absolute
    top: 80px
    left: 100px

  .serviceSwiper
    position: relative
    .swiper-slide
      .content-wrap
        padding-left: 9%
      .num
        padding-top: 80px
      .goal-list
        display: inline-flex
        margin-bottom: 36px
        .item
          margin-right: 16px
      .desc
        margin-bottom: 48px


.clients
  padding-top: 60px
  padding-bottom: 120px
  .bg-orange
    padding: 32px 32px 24px 32px
    margin-bottom: 40px
    position: relative
    z-index: 2
    .title.text-gray
      text-align: right
  .bg-gray
    padding: 68px 40px
    position: relative
    top: -68px
  .scroll-down-lg
    margin-top: 40px


.culture
  padding-top: 0
  padding-bottom: 120px
  position: relative
  .bg-orange
    width: 100%
    height: 550px
    position: absolute
    z-index: 1
  .bg-white
    width: calc(100% - 30px)
    min-height: 550px
    position: absolute
    top: 40px
    z-index: 2
    border: 1px solid $orange
  .content-wrap
    padding-top: 80px
    padding-bottom: 120px
    position: relative
    z-index: 3
    h2.sub-title
      margin-bottom: 48px

  .culture-img
    width: 100%
    display: flex
    img
      width: 33.33%


.team
  padding-top: 0
  padding-bottom: 0
  margin-bottom: -120px
  .bg-orange
    padding-top: 24px
    padding-bottom: 88px
    z-index: 2
  .bg-white
    padding: 40px 50px
    position: relative
    top: -40px
    z-index: 3
    border: 1px solid $orange

  h3.title.two-point-orange-before:before
    top: -3px 

  .product-design
    width: 100%
    position: relative
    top: -280px
    z-index: 2
    .visule
      margin-bottom: 16px
      position: relative
    .product-design-bg
      position: relative
    .product-design-circle
      position: absolute
      top: 36px
      left: 88px



.contact
  background: $black
  .info
    padding-top: 120px
    padding-bottom: 200px
    .logo
      margin-bottom: 72px
    .phone, .email, address
      margin-bottom: 16px
      color: $white
      font-weight: normal
  .contact-bg
    padding: 70px 72px
    position: relative
    top: -88px 
    background: url("../img/md/contact-bg.svg") right top no-repeat

  h2.title.text-orange
    margin-bottom: 60px

  .btn-contact
    display: flex
    margin-top: 32px
    height: 80px
    .h4.text-gray
      height: 80px
      padding: 20px 52px
      color: $white
      background: $orange
    .arrow-block
      height: 80px
      display: flex
      padding: 0 16px
      background: $black
      img
        width: 14px


.backtotop
  position: fixed
  width: 100%
  text-align: right
  bottom: 32px
  z-index: 99
  // bottom: 120px
  img
    margin-right: 4px
    transform: rotate(180deg)
    

.footer
  padding: 30px 0
  background: $black
  color: $white












@media (max-width: 1440px)
  .grid-line
    .horizontal-wrap
      .line
        &:nth-child(n+6)
          display: none
    .vertical-wrap 
      .line
        &:nth-child(n+5)
          display: none


@media (max-width: 960px)
  .grid-line
    .horizontal-wrap
      .line
        &:nth-child(n+5)
          display: none
    .vertical-wrap 
      .line
        &:nth-child(n+4)
          display: none

  .side-menu
    width: 96px
    .logo
      width: 96px
      height: 96px
    .talk-to-us
      width: 96px



@media (max-width: 768px)
  .side-menu 
    display: none

  .m-menu
    position: relative
    display: block
    top: 0
    left: 0
    height: 70px
    width: 100%
    background: #E4E9EF
    transition: all 0.5s ease-out, background 1s ease-out
    transition-delay: 0.2s
    .logo
      width: 70px
      height: 70px
      display: inline-flex
      background: $black
      img
        margin: 0 auto
        width: 37px
        height: 37px
        align-self: center

    .m-nav
      width: 100%
      height: 50px
      position: absolute
      top: 0
      right: 0
      z-index: 99

    .burger-wrap
      position: fixed
      top: 0
      right: 0
      height: 70px
      width: 70px
      cursor: pointer
      transform: rotate(0deg)
      transition: all 0.3s $cubic
      user-select: none
      -webkit-tap-highlight-color: transparent
      z-index: 99
      background: $orange
      .burger
        width: 18px
        height: 18px
        position: relative
        // margin: 0 auto
        top: 40%
        right: -24px
        .bar
          width: 100%
          height: 2px
          display: block
          position: relative
          background: #FFF
          transition: all 0.3s $cubic
          transition-delay: 0s
          &.top
            transform: translateY(0px) rotate(0deg)
          &.middle
            transform: translateY(6px) rotate(0deg)
          &.bottom
            transform: translateY(12px) rotate(0deg)

    ul.menu
      position: fixed
      height: 0
      margin: 0
      padding: 0
      
      // display: none
      overflow: hidden
      list-style: none
      transition: transform 0.5s $cubic, opacity 0.6s $cubic
      li
        text-align: center
        transform: scale(1.15) translateY(-30px)
        opacity: 0
        transition: transform 0.3s $cubic, opacity 0.6s $cubic
        @for $i from 1 through $menuItems
          &:nth-child(#{$i})
            transition-delay: #{0.5 - ($i * 0.05)}s
        a
          display: block
          color: $gray
          font-size: 2rem // 32px
          line-height: 2
          position: relative
          &:active
            background: #EE472E
        a.medium
          font-size: 1rem
          line-height: 4rem
        &.divider
          margin: 24px 32px
          border-top: 1px solid #E4E9EF

    &.m-menu-opened
      height: 100%
      transition: all 0.5s ease-in, background 0.6s ease-in
      transition-delay: 0.25s
      .burger-wrap
        // transform: rotate(90deg)
        .burger
          .bar
            transition: all 0.4s $cubic
            // transition-delay: 0.2s
            &.top
              transform: translateY(4px) rotate(45deg)
            &.middle
              display: none
            &.bottom
              transform: translateY(3px) rotate(-45deg)

      ul.menu
        padding-top: 70px
        height: 100vh
        width: 100%
        opacity: 1
        // display: block
        background: $orange
        li
          transform: scale(1) translateY(0px)
          opacity: 1
          @for $i from 1 through $menuItems
            &:nth-child(#{$i})
              transition-delay: #{0.05 * $i + 0.2}s



  .grid-line 
    .horizontal-wrap
      .line
        &:nth-child(n+4)
          display: flex
        // &:nth-child(n+8)
        //   display: none
    .vertical-wrap
      .line
        &:nth-child(n+4)
          display: flex
        &:nth-child(n+6)
          display: none 

  // .content
  //   padding-top: 50px

  .committed
    padding-top: 40px
    padding-bottom: 40px
    .scroll-down
      margin-bottom: 40px

  .value
    padding-top: 40px
    padding-bottom: 40px
    .bg-orange
      width: 100%
      left: -36px
    .bg-gray
      width: 100%
    .text-wrap
      width: 75%
      margin-left: 0
      h5.sub-title
        margin-right: 0
    .col-12.col-lg-4.offset-lg-1
      padding-top: 140px
    .list
      flex-direction: column
      align-items: center
      .list-img
        margin-bottom: 16px
      .list-content
        text-align: center
      

  .service
    padding-top: 40px
    padding-bottom: 40px
    .bg-gray
      left: -15px
    .bg-orange
      height: 120px
      top: 100px
      left: 30px

    .serviceSwiper 
      .swiper-slide 
        .content-wrap
          padding-left: 0
        .desc
          margin-bottom: 32px
        .arrow-circle-link
          margin-bottom: 32px
        .order-sm-2
          img
            margin-left: -15px
            width: 80%




  .clients
    padding-top: 40px
    padding-bottom: 40px
    .bg-orange
      margin-left: -16px
      margin-bottom: 24px
    .bg-gray
      padding: 32px
      margin: 0 -16px
      top: 0

    .scroll-down-lg
      img
        height: 90px


  .culture
    padding-top: 20px
    padding-bottom: 40px
    .bg-orange
      left: -40px
    .bg-white
      min-height: 920px
      width: calc(100% + 32px)
      left: -16px
      border-top: 1px solid $orange
    .content-wrap
      h2.sub-title
        margin-bottom: 0
      .col-12.col-lg-5.offset-lg-1
        margin-top: 60px
    .culture-img
      flex-direction: column
      img
        width: 100%
        margin: 24px 0


  .team
    padding-top: 40px
    padding-bottom: 40px
    .bg-orange
      padding: 32px
      margin-left: -16px
      width: 80%
    .bg-white
      padding: 60px 32px
      top: 0
      left: -16px
    .list
      text-align: center
    .product-design
      top: 0
      .product-design-circle
        position: relative
        top: 0
        left: 0

  .contact 
    margin-top: 100px
    .info
      padding: 60px 32px
    .contact-bg
      top: 0
      padding: 32px
      margin: 0 -15px



